<template>
	<div class="search-list">
	   <transition-group name="list" tag="ul">
	      <li :key="item" class="search-item" @click="selectItem(item)" v-for="item in searches">
	        <span class="text">{{item}}</span>
	        <span class="icon" @click.stop="deleteOne(item)">
	          <i class="icon-delete"></i>
	        </span>
	      </li>
       </transition-group>
	</div>
</template>
<script>
	export default {
		props: {
			searches: {
				type: Array,
				default: function () {
				    return []
				  }
			}
		},
		methods: {
			selectItem(item) {
				this.$emit('select', item);
			},
			deleteOne(item) {
				this.$emit('delete', item);
			}
		}
	}
</script>
<style scoped lang="stylus" rel="stylesheet/stylesheet">
	.search-list
	  .search-item
	    display: flex
	    align-items: center
	    height: 40px
	    overflow: hidden
	    &.list-enter-active, &.list-leave-active
	      transition: all 0.1s
	    &.list-enter, &list-leave-to
	      height: 0
	    .text
	      flex: 1
	      color: hsla(0,0%,100%,.5)
	    .icon
	      .icon-delete
	        color: hsla(0,0%,100%,.5)
	        font-size: 12px

</style>


